import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  url:string;
  price:number;
  name:string;
  bag:Array<object>=[];

  constructor() { }

  ngOnInit() {
  }

  set(product: string,address: string,val: number){
    this.name=product;
    this.price=val;
    this.url='../../assets/images/'+address+'.jpg';
  }
  addToBag(product :string ,address: string,val: number){
    //console.log("kjdfkjdf");
    this.bag.push({product:[address,val]});
    this.name=product;
        console.log(this.bag);
  }
}
